// Helper function to get image with fallback
export function getImage(category: keyof typeof images, name: string, fallback?: string): string {
  const categoryImages = images[category] as Record<string, string>;
  return categoryImages?.[name] || fallback || "https://placehold.co/800x600/9ca3af/ffffff?text=Image+Not+Found";
}
